import { Injectable } from '@angular/core';

import { TYPE } from 'config/constants/leave';
import { ALLOCATION, STATUS } from 'config/constants/leave-policy';
import { Api } from 'core/services/api.service';
import { EXPIRY } from 'config/constants/compensatory-off';

export class LeaveApproval {

  required: boolean = false;
  from_supervisor: boolean = false;
  users: string[];
}

export class CompensatoryOffExpiry {

  type: EXPIRY;
  count?: number;
}

export class CompensatoryOffApproval {

  from_supervisor: boolean = false;
  users: string[];
}

export class CompensatoryOff {

  expiry: CompensatoryOffExpiry;
  approval: CompensatoryOffApproval;

  constructor() {

    this.expiry = new CompensatoryOffExpiry();
    this.approval = new CompensatoryOffApproval();
  }
}

export class Leave {

  leave_type: TYPE;
  count: number;
  carry_forward: boolean = false;
  maximum_count: number;
  loaned_count: number;
  sandwich: boolean = false;
  allocation: ALLOCATION;

  compensatory_off?: CompensatoryOff;
}

export class LeavePolicy {

  _id: string;
  organization_id: string;
  start_date: Date;
  end_date: Date;
  allowed_back_days: number;
  approval: LeaveApproval = new LeaveApproval();
  leaves: Leave[];
  reiterate: boolean = false;
  leave_policy_status: STATUS;

  constructor() {

    this.leaves = [];
    this.leaves.push(new Leave());
  }

  addLeave() {

    this.leaves.push(new Leave());
  }

  removeLeave(index) {

    this.leaves.splice(index, 1);
  }
}

export class LeavePolicyElement extends LeavePolicy {

  _id: string;
  organization_id: string;
  start_date: Date;
  end_date: Date;
  leaves: Leave[];
  reiterate: boolean = false;
  leave_policy_status: STATUS;
  leave_type: TYPE;
  count: number;
  carry_forward: boolean = false;
  maximum_count: number;
  loaned_count: number;
  sandwich: boolean = false;
  allowed_back_days: number;
  allocation: ALLOCATION;
  approval: LeaveApproval = new LeaveApproval();
}

@Injectable()
export class LeavePolicyService extends Api {

  private _base_url: string = '/leaves/policies';

  getPolicy(filters?: Map<string, string>): Promise<LeavePolicyElement[]> {

    return this.get(this._base_url, filters)
      .then(response => response['data'] as LeavePolicyElement[]);
  }

  postPolicy(policy: LeavePolicy): Promise<Object> {

    return this.post(this._base_url, policy)
      .then(response => response);
  }

  putPolicy(policy: LeavePolicy) {

    return this.put(this._base_url + '/' + policy._id, policy)
      .then(response => response);
  }
}
